import React, { useState, useEffect, useCallback } from 'react';
import { Box, Typography, Paper } from '@mui/material';
import axios from 'axios';
import TitleBar from './TitleBar';
import { API_BASE_URL } from '../config';

function QuoteDisplay() {
    const [quote, setQuote] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    const [fadeOut, setFadeOut] = useState(false);

    // 动画持续时间（毫秒）
    const FADE_DURATION = 1000;  // 增加淡入淡出时间到1秒
    // 内容显示时间（毫秒）
    const DISPLAY_DURATION = 6000;  // 每条内容显示6秒
    // 完整周期时间（显示时间 + 动画时间）
    const TOTAL_DURATION = DISPLAY_DURATION + FADE_DURATION;

    const fetchRandomQuote = useCallback(async () => {
        if (isLoading) return;
        
        try {
            // 1. 开始加载新数据
            setIsLoading(true);
            const response = await axios.get(`${API_BASE_URL}/api/quotes/random`);
            const newQuote = response.data;
            
            // 2. 开始淡出动画
            setFadeOut(true);
            
            // 3. 等待淡出完成
            await new Promise(resolve => setTimeout(resolve, FADE_DURATION / 2));
            
            // 4. 更新内容
            setQuote(newQuote);
            
            // 5. 开始淡入
            setFadeOut(false);
            
        } catch (error) {
            console.error('获取名言失败:', error);
            setFadeOut(false);
        } finally {
            setIsLoading(false);
        }
    }, [isLoading]);

    // 组件挂载时获取第一条名言
    useEffect(() => {
        const initQuote = async () => {
            await fetchRandomQuote();
            // 启动定时器，使用完整周期时间
            const timer = setInterval(fetchRandomQuote, TOTAL_DURATION);
            return () => clearInterval(timer);
        };
        
        const cleanup = initQuote();
        return () => cleanup.then(cleanupFn => cleanupFn());
    }, [fetchRandomQuote, TOTAL_DURATION]); // 添加依赖项

    return (
        <>
            <TitleBar title="每日名言" showSettingsButton={true} />
            <Box
                sx={{
                    minHeight: '100vh',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    backgroundColor: '#f5f5f7',
                    pt: 8,
                    background: 'linear-gradient(135deg, #f5f5f7 0%, #e8e8e8 100%)'
                }}
            >
                <Paper
                    elevation={8}
                    sx={{
                        p: { xs: 3, md: 6 },
                        maxWidth: '800px',
                        width: '90%',
                        textAlign: 'center',
                        backgroundColor: 'rgba(255, 255, 255, 0.9)',
                        backdropFilter: 'blur(10px)',
                        borderRadius: '20px',
                        boxShadow: '0 8px 32px 0 rgba(31, 38, 135, 0.15)',
                        transition: 'all 0.3s ease-in-out',
                        '&:hover': {
                            transform: 'translateY(-5px)',
                            boxShadow: '0 12px 40px 0 rgba(31, 38, 135, 0.18)',
                        },
                        position: 'relative',
                        overflow: 'hidden',
                        '&::before': {
                            content: '""',
                            position: 'absolute',
                            top: 0,
                            left: 0,
                            right: 0,
                            height: '4px',
                            background: 'linear-gradient(90deg, #007FFF 0%, #0059B2 100%)'
                        }
                    }}
                >
                    <Box
                        sx={{
                            opacity: fadeOut ? 0 : 1,
                            transition: `opacity ${FADE_DURATION}ms ease-in-out`
                        }}
                    >
                        {quote ? (
                            <>
                                <Typography 
                                    variant="h4" 
                                    component="div" 
                                    sx={{ 
                                        mb: 4,
                                        fontWeight: 300,
                                        lineHeight: 1.6,
                                        color: '#1a1a1a',
                                        fontFamily: '"Noto Serif SC", serif',
                                        fontSize: { xs: '1.5rem', md: '2rem' }
                                    }}
                                >
                                    "{quote.content}"
                                </Typography>
                                <Typography 
                                    variant="h6" 
                                    sx={{ 
                                        fontWeight: 400,
                                        color: '#666',
                                        textAlign: 'right',
                                        fontStyle: 'italic',
                                        mt: 2,
                                        fontSize: { xs: '1rem', md: '1.25rem' }
                                    }}
                                >
                                    —— {quote.author}
                                </Typography>
                            </>
                        ) : (
                            <Typography>加载中...</Typography>
                        )}
                    </Box>
                </Paper>
            </Box>
        </>
    );
}

export default QuoteDisplay; 